"use client";
import { useState, useEffect } from "react";

const useBanner = () => {
    return {
        data: [
            {
                id: 1,
                link_url: "https://example.com/promo1",
                image_url: "https://images.unsplash.com/photo-1557804506-669a67965ba0?ixlib=rb-4.0.3&auto=format&fit=crop&w=1774&q=80" // Placeholder image
            },
            {
                id: 2,
                link_url: "https://example.com/promo2",
                image_url: "https://images.unsplash.com/photo-1542831371-29b0f74f9713?ixlib=rb-4.0.3&auto=format&fit=crop&w=1770&q=80" // Placeholder image
            },
            {
                id: 3,
                link_url: "https://example.com/promo3",
                image_url: "https://images.unsplash.com/photo-1550684848-fac1c5b4e853?ixlib=rb-4.0.3&auto=format&fit=crop&w=1770&q=80" // Placeholder image
            }
        ],
        isLoading: false,
        isError: false
    };
};

export default function BigBanner() {
    const [current, setCurrent] = useState(0);
    const { data: ads, isLoading, isError } = useBanner();

    useEffect(() => {
        // Only start the interval if we have ads
        if (!ads || ads.length === 0) return;

        const timer = setInterval(() => {
            setCurrent((prev) => (prev + 1) % ads.length);
        }, 5000);

        return () => clearInterval(timer);
    }, [ads]);

    if (isLoading) return <div className="w-full h-[200px] md:h-[400px] bg-gray-100 animate-pulse rounded-xl my-6 flex items-center justify-center text-gray-400">Loading...</div>;

    if (isError) return <div className="hidden"></div>;

    if (!ads || ads.length === 0) return null;

    return (
        <div className="relative w-full h-[200px] md:h-[400px] overflow-hidden rounded-xl shadow-lg my-6 group bg-gray-50">
            {ads.map((ad, index) => (
                <a
                    key={ad.id || index}
                    href={ad.link_url}
                    target="_blank"
                    rel="noopener noreferrer"
                    className={`absolute inset-0 transition-opacity duration-1000 ease-in-out block ${
                        index === current ? "opacity-100 z-10" : "opacity-0 z-0"
                    }`}
                >
                    {/* Replaced Next.js Image with standard img tag for preview compatibility */}
                    <img
                        src={ad.image_url} // Using the direct URL from mock data for preview
                        alt="Advertisement"
                        className="w-full h-full object-cover"
                    />
                </a>
            ))}

            {/* Navigation Dots */}
            {ads.length > 1 && (
                <div className="absolute bottom-4 left-1/2 transform -translate-x-1/2 flex space-x-2 z-20">
                    {ads.map((_, idx) => (
                        <button
                            key={idx}
                            onClick={() => setCurrent(idx)}
                            className={`w-3 h-3 rounded-full transition-colors duration-300 ${
                                current === idx ? "bg-white scale-110 shadow-md" : "bg-white/50 hover:bg-white/80"
                            }`}
                            aria-label={`Go to slide ${idx + 1}`}
                        />
                    ))}
                </div>
            )}
        </div>
    );
}